@mixin pad() {
    @media(max-width:992px) {
        @content;
    }
}

@mixin mobile() {
    @media(max-width:767px) {
        @content;
    }
}

@mixin background() {
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    @content;
}

//layout icon
@mixin iconpoint() {
    cursor: pointer;
    @content;
}

